
  <template>
    <div>
      <Split v-model="split1">
        <div slot="left" class="center-card-s">
          <div style="margin-bottom: 10px;">
            <p>{{ queryParams.version }} 覆盖的commit列表</p>
          </div>
          <div>
            <Timeline>
              <TimelineItem v-for="(item, index) in commitList" v-if="commitList" :key="index">
                <a class="content" href="javascript:void(0)" @click="setCommit(item)">{{item}}</a>
              </TimelineItem>
            </Timeline>
          </div>
        </div>
        <div
          slot="right"
          class="center-card-s"
          v-if="commitData.length !== 0"
        >
          <div :key="index" v-for="(item, index) in commitData">
            <Divider orientation="left" style="font-size: 0.6em;font-style: italic;">{{item.scenes}}</Divider>
            <Table border
              :columns="columns"
              :data="item.data"
            >
            </Table>
          </div>
        </div>
        <div
          slot="right"
          class="center-card-s"
          v-else
        >
          <p align="center" style="font-size: 16px">暂未有任务覆盖</p>
        </div>
      </Split>
    </div>
</template>

<script>
import api from '../api/index';
import {CommitsUrl, CommitDetailUrl} from '../api/url.js';
import IntegrationTest from './IntegrationTest.vue';
export default {
  name: 'CommitDetails',
  props: {},
  data: function () {
    return {
      search: {
        page: 1,
        pagesize: 20
      },
      columns: [
        {
          title: '任务',
          key: 'tname',
          align: 'center',
          render: (h, params) => {
            return h('div', [h('a', {
              attrs: {
                href: 'javascript:void(0)'
              },
              on: {
                click: () => {
                  this.jumper(params.row);
                }
              }
            }, params.row.tname)
            ]);
          }
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            const { status } = params.row;
            return h('div', [
                h('Tag', {
                props: {
                  color: ['passed', 'success', 'pass'].indexOf(status.toLowerCase()) >= 0 ? 'green' : 'red'
                }
                }, status)
            ]);
          }
        }
      ],
      queryParams: {},
      commitList: [],
      split1: 0.3,
      commitData: [],
      selectCommit: ''
    };
  },
  mounted: function () {
    this.queryParams = this.$route.query;
    console.log('this query params', this.queryParams);
    this.getData();
  },
  computed: {
  },
  components: {
    IntegrationTest
  },
  methods: {
    async getData() {
      let params = {
        version: this.queryParams.version,
        page: this.search.page,
        pagesize: this.search.pagesize
      }
      const {code, data, msg} = await api.get(CommitsUrl, params);
      if (parseInt(code, 10) === 200) {
        this.commitList = data;
        this.selectCommit = this.commitList[0];
        this.getCommitDetail();
      } else {
        this.commitList = [];
        this.$Message.error({
          content: '请求出错: ' + msg,
          duration: 30,
          closable: true
        });
      }
    },
    setCommit(commit) {
      this.selectCommit = commit;
      this.getCommitDetail();
    },
    async getCommitDetail() {
      let params = {
        version: this.queryParams.version,
        commit: this.selectCommit
      }
      const {code, data, msg} = await api.get(CommitDetailUrl, params);
      if (parseInt(code, 10) === 200) {
        this.commitData = data;
      } else {
        this.commitData = [];
        this.$Message.error({
          content: '请求commit详情出错: ' + msg,
          duration: 30,
          closable: true
        });
      }
    },
    jumper(item) {
      // 还是根据任务的type来确定跳转到function还是model，目前暂时都用ApiDetails
      let _params = {};
      _params = Object.assign(_params, item);
      const { href } = this.$router.resolve({name: 'ApiDetails', query: _params});
      window.open(href, '_blank');
    }
  }
};
</script>
<style scoped>
.center-card-s {
  width: 98%;
  max-height: 861px;
  overflow:auto;
  margin-bottom: 2%
}
</style>


