<template>
<div>
  <div>
    <Divider orientation="left" style="font-size: 0.5em;font-style: italic;">Release测试阶段</Divider>
  </div>
  <div>
    <Steps>
      <Step
        :status="item.status"
        :content="item.content" v-for="(item, key, index) in allSteps" 
        :key="key"
        @click.native="showModal(key, item)">
      </Step>
    </Steps>
  </div>
  <div>
    <Divider orientation="left" style="font-size: 0.5em;font-style: italic;">进度详情</Divider>
  </div>
  <div style="margin-bottom: 1.5%">
    <div v-if="allSteps.integration !== undefined && allSteps.integration.flag">
      <Intergration
        :repoInfo="repoInfo"
        :integrationData="integrationData"
        :bugData="bugData">
      </Intergration>
    </div>
    <div v-else-if="allSteps.regression !== undefined && allSteps.regression.flag">
      <regression :data="bugData"></regression>
    </div>
    <div v-else-if="allSteps.createtag !== undefined && allSteps.createtag.flag">
      <Intergration
        :repoInfo="repoInfo"
        :integrationData="integrationData"
        :bugData="bugData">
      </Intergration>
    </div>
    <div v-else-if="allSteps.release !== undefined && allSteps.release.flag">
     <!--这里呈现编包验证的结果即可-->
      <regression :data="bugData"></regression>
    </div>
    <div v-else>
      <Intergration
        :repoInfo="repoInfo"
        :integrationData="integrationData"
        :bugData="bugData">
      </Intergration>
    </div>
  </div>
  <div>
    <Modal v-model="allSteps.createtag !== undefined && allSteps.createtag.flag" title="快速打tag" @on-cancel="handleReset" width="600px">
        <Form ref="addForm" :model="tagForm" :label-width="80">
          <FormItem label="分支" prop="branch">
            <Input disabled v-model="tagForm.branch"/>
          </FormItem>
          <FormItem label="commit: " prop="commit">
            <Input v-model="tagForm.commit"/>
          </FormItem>
          <FormItem label="tag名称: " prop="tagName">
            <Input v-model="tagForm.tagName"/>
          </FormItem>
        </Form>
        <div slot="footer">
          <Button type="text" @click="handleReset">取消</Button>
          <Button type="primary" @click="handleSubmit">确定</Button>
        </div>
      </Modal>
  </div>
</div>
</template>

<script>
import api from '../api/index';
import { ReleaseVersionUrl, BugUrl } from '../api/url.js';
import BaseInfo from './BaseInfo.vue';
import { dateFmt } from '../util/help.js';
import Intergration from './Intergration.vue';
import Regression from './Regression.vue';

export default {
  data: function () {
    return {
      tagForm: {
        branch: '',
        tagName: '',
        commit: ''
      },
      versionName: '',
      routeParams: this.$route.params,
      repoInfo: {},
      allSteps: {},
      integrationData: {
        data: []
      },
      bugData: []
    }
  },
  mounted: function () {
    this.initData();
    this.getData();
  },
  watch: {
    $route() {
      this.routeParams = this.$route.params;
      this.initData();
      this.getData();
    }
  },
  components: {
    BaseInfo,
    Intergration,
    Regression
  },
  computed: {
  },
  methods: {
    async getData() {
      // 判断参数如果参数中有tag，则name=version；如果没有，则name=release+version
      if ('tag' in this.routeParams) {
        this.versionName = this.routeParams.version;
      } else {
        // 如果version是空
        this.versionName = 'release/' + this.routeParams.version;
        // 如果前端是主页；则需要后端来做数据的呈现逻辑
        // 如果前端传递的versionName=release/undefined; 则默认返回当前处于激活状态的数据即可
        // release/2.3.0 或者v2.2.2等 如果给定了确定版本，则严格按照name==release去筛选
      }
      let _params = {'version': this.versionName}
      console.log("queury params", _params)
      const {code, data, msg} = await api.get(ReleaseVersionUrl, _params);
      // 获取任务进展数据
      // 获取bug数据
      console.log("code", code)
      if (parseInt(code) == 200) {
        this.repoInfo = data.repo_info;
        this.allSteps = data.all_steps;
        this.tagForm.branch = this.repoInfo.branch;
        this.integrationData = data.integration_data;
      } else {
        this.repoInfo = {}
        this.allSteps = {}
        this.$Message.error({
          content: '请求出错: ' + msg,
          duration: 30,
          closable: true
        })
      }
      await this.getBugData();
    },
    async getBugData() {
      let _params = {'tag': this.repoInfo.tag};
      const {code, data, msg} = await api.get(BugUrl, _params);
      if (parseInt(code, 10) === 200) {
        this.bugData = data;
      } else {
        this.bugData = [];
        this.$Message.error({
          content: '请求出错: ' + msg,
          duration: 30,
          closable: true
        });
      }
    },
    async selectDate() {
      this.getData();
    },
    initDate() {
      let day1 = new Date();
      day1.setDate(day1.getDate() - 1);
      return day1;
    },
    initData() {
      this.tagForm.tagName = '';
      this.tagForm.commit = '';
    },
    handleReset(auto) {
      this.allSteps.createtag.flag = false;
      this.initData(auto);
    },
    async handleSubmit(auto) {
      console.log('content of this tagForm is', this.tagForm);
      await this.getData();
      // 成功或失败，都需要关闭窗口
      this.initData(auto);
    },
    showModal(key, item) {
      let status = item.status;
      if (status == 'wait') {
        this.$Message.info({
          content: "请先保证任务通过并修复卡片，请稍后再试。。。",
          duration: 3,
          closable: true
        });
      } else {
        for (let ky in this.allSteps) {
          this.allSteps[ky].flag = false;
        }
        if (key == 'createtag') {
          if (status == 'process') {
            this.allSteps[key].flag = true;
          }
        } else {
          this.allSteps[key].flag = true;
        }
      }
    },
    jumper(row) {
      let _params = {
        'tid': row.tid,
        'tname': row.project_name,
        'build_type_id': row.build_type_id
      };
      _params = Object.assign(_params, row);
      // 打开新的页面展示详情
      const { href } = this.$router.resolve({name: row.task_type, query: _params});
      window.open(href, '_blank');
    }
  }
};
</script>

<style scoped>
.all-line-row {
  margin-bottom: 0.5%;
  margin-left: 0.5%;
}
.center-card-s {
    width: 100%;
    max-height: 600px;
    overflow:auto;
  }
</style>



